<template>
  <div class="upload-psd-btn-wrapper">
    <el-upload
        drag
        action="https://jsonplaceholder.typicode.com/posts/"
         :before-upload="beforeUpload"
        accept=".psd"
        :show-file-list="false"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">PSD40MB</div>
        <slot></slot>
    </el-upload>
    <div style="width:100%;text-align:center;margin-top:30px;">
          <el-radio v-model="psdModel" label="text" border></el-radio>
          <el-radio v-model="psdModel" label="image" border></el-radio>
        </div>
  </div>
</template>

<script>
	export default {
    data() {
      return {
        loading:"",
        psdModel:"image"
      }
    },
		methods: {
			beforeUpload(file){
        this.loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
				if(file.size > 40 * 1024 * 1024){
					this.$message.error('psd40M')
					return;
				}
				let temp1 = file.name.split('.')
				let temp = temp1[temp1.length - 1]
				if (!['psd'].includes(temp)) {
					this.$message.error('psd')
					return false;
				}
        this.uploadPsd(file);

				return false;
      },
      uploadPsd(file){
				let params  = new FormData()
        params.append('file', file);
        params.append('psdModel',this.psdModel)
				this.$API.psdPpload(params).then(res => {
          this.$emit('upload-success', res.body || [])
          this.loading.close();
        })
      }
    }
	}
</script>

<style lang="scss" scoped>
.upload-psd-btn-wrapper{
  display: inline-block;
}
</style>
