import screenfull from 'screenfull' //
/**
 *  
 * */
export default {
  methods: {
    oncontextmenu(event) {
      // console.log(this.activeElementUUID)
      this.$contextmenu({
        items: [{
            label: "(C)",
            icon: "el-icon-copy-document",
            disabled: !this.activeElementUUID, //
            onClick: () => {
              this.Copy()
            }
          },
          {
            label: "(V)",
            icon: "el-icon-money",
            disabled: !this.activeEle, //
            onClick: () => {
              this.Paste()
            }
          },
          {
            label: "(S)",
            icon: "el-icon-folder-checked",
            onClick: () => {
              this.$emit('save')
              this.$message.success("!");
            }
          },
          {
            label: "(D)",
            icon: "el-icon-delete",
            onClick: () => {
              this.$store.dispatch("elementCommand", "delete");
            }
          },
          {
            label: "(R)",
            divided: true,
            onClick: () => {
              this.$store.dispatch("resetPageData")
            }
          },
          {
            label: "/(Y)",
            divided: true,
            icon: "el-icon-turn-off",
            // disabled: this.historyCache.length != 0,
            onClick: () => {
              this.ishowHistory = !this.ishowHistory
            }
          },
          {
            label: "(R)",
            icon: "el-icon-refresh",
            onClick: () => {
              this.$router.go(0);
            }
          },
          {
            label: "/(Q)",
            icon: "el-icon-full-screen",
            onClick: () => {
              screenfull.toggle();
            }
          }
        ],
        event,
        customClass: "class-a",
        zIndex: 3,
        minWidth: 230
      });
      return false;
    },
    disableMenu() {
      return false;
    }
  }

}